/* src/App.css */

/* --- REGLAS GLOBALES --- */
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #f0f8ff !important; /* Tu regla de fondo global */
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

/* Estilos generales para el contenedor principal de la aplicaci칩n */
.app-container {
    display: flex;
    flex-direction: column;
    min-height: 100vh; /* Asegura que la aplicaci칩n ocupe al menos toda la altura de la vista */
    overflow-x: hidden !important; /* Previene scroll horizontal no deseado */
    position: relative;
    z-index: 1;
}

/* --- ESTILOS PARA EL ENCABEZADO (HEADER) --- */
header {
    background-color: #FFD700 !important; /* 춰AMARILLO PARA EL HEADER! */
    color: #333 !important; /* Texto oscuro para contrastar con el amarillo */
    padding: 15px 20px; /* Ajustado a un padding uniforme */
    display: flex; /* Para alinear el logo y la navegaci칩n */
    justify-content: space-between; /* Espacio entre el logo y la nav */
    align-items: center; /* Centra verticalmente los elementos */
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* Sombra ligera */
    position: sticky; /* Para que el header se quede arriba al hacer scroll */
    top: 0;
    width: 100%;
    z-index: 1000; /* Asegura que est칠 por encima de otros contenidos */
    box-sizing: border-box; /* IMPORTANTE: Asegura que el padding no a침ada al ancho total */
}

.logo {
    font-size: 1.8em;
    font-weight: bold;
    color: #333 !important; /* Asegura que el logo tambi칠n sea oscuro */
    margin-right: auto; /* Empuja el logo a la izquierda */
    padding-left: 5px; /* Peque침o padding para que no se pegue al borde izquierdo */
}

nav {
    padding-right: 5px; /* Peque침o padding para que el 칰ltimo elemento no se pegue al borde derecho */
}

nav ul {
    list-style: none; /* Elimina las vi침etas de la lista */
    padding: 0;
    margin: 0;
    display: flex; /* Para que los 칤tems del men칰 est칠n en fila */
    gap: 25px; /* Espacio entre los 칤tems del men칰 */
}

nav ul li a {
    color: #333 !important; /* Texto oscuro para los enlaces */
    text-decoration: none; /* Elimina el subrayado de los enlaces */
    font-size: 1.1em;
    font-weight: 600; /* Un poco m치s de negrita */
    transition: color 0.3s ease; /* Transici칩n suave al pasar el rat칩n */
    padding: 5px 0; /* Peque침o padding para mejorar el 치rea clicable */
    white-space: nowrap; /* Evita que el texto "Inicio" se corte en varias l칤neas */
}

nav ul li a:hover {
    color: #8B4513 !important; /* Un color marr칩n suave al pasar el rat칩n */
}

/* Estilos para el contenido principal (Main) */
main {
    flex-grow: 1; /* Esto hace que el contenido principal ocupe todo el espacio disponible entre el header y el footer */
}

/* Estilos para el pie de p치gina (Footer) */
footer {
    background-color: #333; /* Fondo oscuro para el footer */
    color: white;
    text-align: center;
    padding: 20px;
    font-size: 0.9em;
    width: 100%; /* Asegura que el footer ocupe todo el ancho */
    margin-top: auto; /* Empuja el footer al final si el contenido es corto */
    box-sizing: border-box; /* Asegura que el padding no a침ada al ancho total */
}

footer p {
    margin: 0; /* Elimina el margen predeterminado del p치rrafo en el footer */
}

/* --- Media Queries para responsividad del APP.CSS --- */
@media (max-width: 768px) {
    header {
        flex-direction: column; /* Apila el logo y la navegaci칩n en m칩viles */
        padding: 10px 15px; /* Ajustado para m칩viles */
        align-items: flex-start; /* Alinea el logo a la izquierda en vista de columna */
    }

    .logo {
        margin-bottom: 10px; /* Espacio entre el logo y el men칰 apilado */
        margin-right: 0; /* Elimina margin-right auto en m칩vil */
    }

    nav {
        width: 100%; /* La navegaci칩n ocupa todo el ancho */
        padding-right: 0; /* Elimina padding-right en m칩vil */
    }

    nav ul {
        flex-direction: column; /* Apila los 칤tems del men칰 */
        width: 100%; /* Los 칤tems de la lista tambi칠n ocupan todo el ancho */
        gap: 5px; /* Reduce el espacio entre 칤tems en m칩vil */
        text-align: left; /* Alinea el texto del men칰 a la izquierda en m칩vil */
    }

    nav ul li {
        width: 100%; /* Cada 칤tem de la lista ocupa todo el ancho */
    }

    nav ul li a {
        display: block; /* Hace que el enlace ocupe todo el ancho de su li */
        padding: 8px 10px; /* Ajusta el padding para un mejor 치rea de clic */
        text-align: center; /* Centra el texto del enlace en m칩vil */
    }
}