/* src/components/SobreNosotros.css */

/* Estilos para la secci칩n principal de "Sobre Nosotros" */
.seccion-sobre-nosotros {
    padding: 40px 20px; /* Espacio alrededor del contenido */
    max-width: 1200px; /* Ancho m치ximo para centrar el contenido */
    margin: 20px auto; /* Centra la secci칩n en la p치gina */
    background-color: #fff; /* Fondo blanco para el recuadro */
    border-radius: 8px; /* Bordes redondeados */
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1); /* Sombra suave */
    text-align: center; /* Centra el t칤tulo y la l칤nea */
}

/* T칤tulo principal "Sobre Nosotros" */
.titulo-seccion-snack {
    color: #FFD700; /* Amarillo como en tu dise침o */
    font-size: 2.5em;
    margin-bottom: 10px;
}

/* L칤nea decorativa debajo del t칤tulo */
.linea-decorativa-amarilla {
    border: none;
    border-top: 3px solid #FFD700; /* L칤nea amarilla */
    width: 80px; /* Ancho de la l칤nea */
    margin: 0 auto 30px auto; /* Centra la l칤nea y da espacio abajo */
}

/* Contenedor GRID para las dos columnas */
.contenido-nosotros-grid {
    display: grid;
    /* Define dos columnas: la primera para la imagen (1 parte), la segunda para el texto (2 partes) */
    grid-template-columns: 1fr 2fr;
    gap: 40px; /* Espacio entre las columnas */
    align-items: center; /* Centra verticalmente el contenido de las columnas */
    text-align: left; /* Alinea el texto a la izquierda dentro de sus columnas */
    padding: 20px;
}

/* Columna de la imagen */
.imagen-nosotros-columna {
    display: flex; /* Para centrar la imagen si es m치s peque침a */
    justify-content: center;
    align-items: center;
}

.imagen-tostilocos {
    max-width: 100%; /* La imagen no exceder치 el ancho de su columna */
    height: auto; /* Mantiene la proporci칩n de la imagen */
    border-radius: 8px; /* Opcional: bordes redondeados para la imagen */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Sombra para la imagen */
}

/* Columna del texto de la historia */
.texto-historia-columna {
    /* No se necesitan estilos complejos aqu칤, ya que el grid lo maneja */
}

.subtitulo-historia {
    color: #333; /* Color oscuro para el subt칤tulo */
    font-size: 1.8em;
    margin-bottom: 15px;
    font-weight: 600;
}

.texto-historia-columna p {
    line-height: 1.7; /* Espaciado entre l칤neas para mejor legibilidad */
    margin-bottom: 15px;
    color: #555; /* Un gris m치s suave para el texto del p치rrafo */
    font-size: 1.05em;
}

/* Estilo para la frase final */
.frase-final-nosotros {
    font-weight: bold;
    color: #FFD700; /* Amarillo para la frase final */
    font-size: 1.1em;
    margin-top: 25px;
}

/* Media Queries para responsividad (para pantallas m치s peque침as) */
@media (max-width: 768px) {
    .contenido-nosotros-grid {
        grid-template-columns: 1fr; /* Cambia a una sola columna en pantallas peque침as */
        gap: 20px; /* Reduce el espacio */
        text-align: center; /* Centra el texto y la imagen */
    }

    .texto-historia-columna {
        padding: 0; /* Elimina padding adicional en m칩vil */
    }

    .titulo-seccion-snack {
        font-size: 2em; /* Ajusta el tama침o del t칤tulo */
    }

    .subtitulo-historia {
        font-size: 1.5em; /* Ajusta el tama침o del subt칤tulo */
    }
}